export function getCollection() {
  let collections = localStorage.getItem("collection");
  collections = collections ? JSON.parse(collections) : [];
  return collections;
}

export function setCollection(collection) {
  localStorage.setItem("collection", JSON.stringify(collection));
}

export function addGoodToCollection(good) {
  const is_in_collection = isInCollection(good.id);
  const collections = getCollection();
  if (!is_in_collection) {
    collections.push(good);
    setCollection(collections);
  }
}

export function isInCollection(id) {
  const collections = getCollection();
  const index = collections.findIndex((item) => item.id === id);
  return index !== -1;
}

export function deleteGoodFromCollection(id) {
  const is_in_collection = isInCollection(id);
  if (is_in_collection) {
    let collections = getCollection();
    const index = collections.findIndex((item) => item.id === id);
    collections.splice(index, 1);
    setCollection(collections);
  }
}
