import React, { useState } from "react";
import img from "../assets/banner.jpg";
// import SearchBarExample from './SearchBar'
import Crouse from "./Carouse";
import { SearchBar } from "antd-mobile";
import Search2 from "./Search2";
import Another from "./Another";
import "./style.css";
import { withRouter } from "react-router-dom";
import pic from "./tb.png";
import goodsList from "../mock/goods-list";
import goods_list from "../mock/goods-list";
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
const Home = (props) => {
  const [listData, setListData] = useState(goods_list);

  const Items = listData.map((item, index) => {
    return (
  
        <div className="double" key={item.id} onClick={
            () => props.history.push(`/another/${item.id}`)
        }>
          <img src={item.poster} />
          <p>{item.name}</p>
          <p>
            <span>{item.price}块钱</span>
            <span>{item.buyNum}人购买</span>
          </p>
        </div>
     
    );
  });

  return (
    <div>
      {/* <SearchBarExample /> */}
      {/* <Search2/> */}
      <div className="search2">
        <img src={pic} />
        <div className="child" onClick={() => props.history.push("/search2")}>
          <p>点击此处搜索</p>
        </div>
      </div>
      <Crouse />
      <div className="list-wrapper">
        {Items}
      </div>
    </div>
  );
};

export default withRouter(Home);
