import React, { useState } from "react";
import { getCollection, deleteGoodFromCollection} from "../mock/collectionStore";
import { isLogin } from "../mock/user";

function Collection(props) {
  const is_login = isLogin();
  if (!is_login) {
    props.history.replace('/login');

  } 
  const [collections, setCollections] = useState(getCollection());
  const handleClickDelete = (id) => {
    deleteGoodFromCollection(id);
    setCollections(getCollection());
  }
  const Items = collections.map((item) => {
    return (
      <div className="good-item">
        <div className="good-name">
          <span>{item.name}</span>
          <span>{item.price}</span>
        </div>
        <button onClick={() => handleClickDelete(item.id)}>删除</button>
      </div>
    );
  });
  return <div className="collection-wrapper">
    {Items}
  </div>;
}

export default Collection;
