import React, { useState } from "react";
import { Button, TabBar } from "antd-mobile";
import { withRouter } from "react-router-dom";
import goods_list from "../mock/goods-list";
import { isLogin, login } from "../mock/user";
import { isInCollection, addGoodToCollection } from "../mock/collectionStore";
const Another = (props) => {
  const good_id = Number(props.match.params.id);
  const good = goods_list.find((item) => item.id === good_id);
  const is_in_collection = isInCollection(good_id);
  const [myIcon, setMyIcon] = useState(
    is_in_collection ? (
      <i className="iconfont icon-star-opp"></i>
    ) : (
      <i className="iconfont icon-shoucang"></i>
    )
  );
  return (
    <div>
      <img src={good.poster} />
      <p>{good.name}</p>
      <p>{good.price}元</p>
      <p>{good.buyNum}人已购买</p>
      <button onClick={() => props.history.push("/Home")}>返回</button>
      <div
        style={{
          position: "fixed",
          width: "100%",
          bottom: 0,
        }}
      >
        <TabBar
          unselectedTintColor="#949494"
          tintColor="red"
          barTintColor="white"

          // tabBarPosition="buttom"
          // hidden={this.state.hidden}
          // prerenderingSiblingsNumber={0}
        >
          <TabBar.Item
            title="店铺"
            key="home"
            icon={<i className="iconfont icon-dianpu"></i>}
            selectedIcon={<i className="iconfont icon-yemian-copy-copy"></i>}
            // selected={ pathname === '/' }
            onPress={() => {
              props.history.push("/");
            }}
            data-seed="logId"
          ></TabBar.Item>
          <TabBar.Item
            title="客服"
            key="cart"
            icon={<i className="iconfont icon-kefu"></i>}
            selectedIcon={<i className="iconfont icon-gouwuche1"></i>}
            // selected={ pathname === '/cart' }
            onPress={() => {
              props.history.push("/cart");
            }}
            data-seed="logId1"
          ></TabBar.Item>
          <TabBar.Item
            title="收藏"
            key="Friend"
            icon={myIcon}
            selectedIcon={<i className="iconfont icon-liebiao1"></i>}
            // selected={ pathname === '/dingdan' }
            onPress={() => {
              if (!isLogin()) {
                  props.history.replace("/login");
              } else {
                setMyIcon(<i className="iconfont icon-star-opp"></i>);
                addGoodToCollection(good);
              }

              //props.history.push("/dingdan");
            }}
            data-seed="logId2"
          ></TabBar.Item>
          <TabBar.Item
            title="加入购物车"
            key="my"
            icon={<i className=""></i>}
            selectedIcon={<i className="iconfont icon-041"></i>}
            // selected={ pathname === '/mytaobao' }
            onPress={() => {
              props.history.push("/mytaobao");
            }}
            data-seed="logId3"
          ></TabBar.Item>
          <TabBar.Item
            title="立即购买"
            key="my"
            icon={<i className=""></i>}
            selectedIcon={<i className="iconfont icon-041"></i>}
            // selected={ pathname === '/mytaobao' }
            onPress={() => {
              props.history.push("/mytaobao");
            }}
            data-seed="logId3"
          ></TabBar.Item>
        </TabBar>
      </div>
    </div>
  );
};

export default Another;
